*{	margin: 0;	padding: 0; }
.upper{	text-transform: uppercase;}
h1{
	letter-spacing: 0.1em;
	font-size: 1.3em;
	color: white;
	padding: 1em;
}
span{
    font-family: 'Playfair Display', serif;
}
hr{
	margin-left: 5em;
	width: 50vw;
}
.p-hero{
	margin-top: 1em;
	font-size: 0.8em;
	color: rgba(500,250,500,0.6);
	padding: 1em;
}
.row-top{
	width: 100%;
	height: 7%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-align: center;
	    align-items: center;
}.row-center{
	width: 100vw;
	height: 93vh;
	text-align:center;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.logotipo{
	padding: 1em;
	margin-left: 2em;
}
.img-logo{
	width: 4em;
	height: 1em;
}
.img-logotipo{
	width: 6em;
	height: 6em;
	margin-bottom:2em; 
}
.hero-wrapper{
	background: center;
	background-size: cover;
	background-image:url(./assets/bgintro.jpg); 
	height: 100vh;
	width: 100vw;
}


