
/* Telefones em paisagem a tablet em retrato */
@media ( min-width: 539px ) and (max-width: 767px) {
    /*HERO*/    
   	hr{
  		margin-left: 8em;
    }
    .img-logo{
	width: 5em;
	height: 2em;
	}
    h1{
    	font-size: 2em;
    }
    .p-hero{
		font-size: 1em;
	}
    .img-logotipo{
    	width: 8em;
    	height: 8em;
    }
    /*SERVICES*/
    h2.services{
    	padding: 1em;
		font-size: 1.7em;
	}
	.services-wrapper{
		height: 85vh;
	}
	.column-services{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: column wrap;
		    flex-flow: column wrap;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: center;
		    align-items: center;
		width: 30%;
		height: 100%;
	}
	.column-services{
		margin: 0 1em 0 0;
	}
	.image-services{
		margin-top: 4em;
		width: 6em;
		height: 6em;
	}
	.p-service{
		margin-top: 1em;
		font-size: 1em;
	}
	h3{
		margin-top: 1em;
		font-size: 1em;
	}
	/*CONTACTS*/
	.contacts-wrapper{
		width: 100vw;
		height: 85vh;
	}
	h2.contact{
		padding: 2em 0em 0em 2em;
		font-size: 1.7em;
	}
	.row-answear-contact{
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: column wrap;
		    flex-flow: column wrap;
	}
	.row-email-contact{
		text-align: center;
		width: 100%;
		height: 30%;
	}
	p.contact{
		margin-top: 1em;
		font-size: 1em;
	}
	h4.upper{
		font-size: 1em;
	}
	.row-socials{
		width: 100%;
		height: 30%;
	}
}
 
